import { Injectable } from '@angular/core';
import { Http,Response } from '@angular/http';
import 'rxjs/add/operator/map';
import {ConstantService} from  '../constant.services'; 

@Injectable()
export class TodoService {
 
  constructor(private _http:Http,public ConstantService: ConstantService) { }
  checkMe:any;
  options;

  addTodo(info){
    //console.log(info)
   let url = this.ConstantService.API_ENDPOINT+'createTodo';
    return this._http.post(url,info)
      .map(res=>res.json());
    }

    getLead(){
      let url = this.ConstantService.API_ENDPOINT+'listTodo';
      return this._http.get(url);
    }
}



